/* table of contents 
---------------------------------------

1. Responsiveness tablet
---------------------------------------

1.1 general resize by changing the font size in html
1.2 Default box resizing
1.3 game area
1.3.1. memory blocks styling
1.4 highscore modal


2. Responsiveness Tablet (801px) 
---------------------------------------

2.1 general resize by changing the font size in html
2.2 Default box resizing  
2.3. game area 
2.3.1 memory blocks styling 
2.4. Difficulty select menu 


3. Responsiveness Mobile 641px 
---------------------------------------

3.1 general resize by changing the font size in html
3.2 Default box resizing 
3.3. game area
3.3.1 game area layout 
3.3.1 memory blocks styling
3.3.1.1 memory blocks styling wdith
3.3.1.2 memory blocks styling height
3.3.2 bottom area
3.4 highscore modal
3.5 Difficulty select menu
3.6 Custom difficulty select menu

*/

@media (max-width: 1281px) {
  /* 1.1 general resize by changing the font size in html*/
  html {
    font-size: 60%;
  }

  /* 1.2 Default box resizing  */
  .default-box {
    width: 90vw;
    height: 90vh;

    margin: 0rem 10rem;
  }

  /* 1.3 game area */
  /* 1.3.1. memory blocks styling */

  .memory-block-and-start .margin--30 {
    margin: 0rem 25%;
  }

  .memory-block-and-start .margin--20 {
    margin: 0rem 15%;
  }

  .memory-block-and-start .margin--15 {
    margin: 0rem 10%;
  }

  .memory-block-and-start .margin--10 {
    margin: 0rem 5%;
  }

  /* 1.4 highscore modal */

  .high-score-modal-box {
    height: 70%;
    width: 70%;
  }
}

/*
2. Responsiveness Tablet 801px 
*/

@media (max-width: 801px) {
  /* 2.1 general resize by changing the font size in html*/
  html {
    font-size: 55%;
  }

  /* 2.2 Default box resizing  */
  .default-box {
    width: 92.5vw;
    height: 92.5vh;
  }

  /* 2.3. game area */
  /* 2.3.1 memory blocks styling */

  .memory-block-and-start .margin--30 {
    margin: 0rem 15%;
  }

  .memory-block-and-start .margin--20 {
    margin: 0rem 10%;
  }

  .memory-block-and-start .margin--15 {
    margin: 0rem 10%;
  }

  .memory-block-and-start .margin--10 {
    margin: 0rem 5%;
  }

  /* 2.4. Difficulty select menu */

  .difficulty-buttons {
    flex-basis: 60%;
    gap: 4rem;
  }
}

/* 3. Responsiveness Mobile 641px */

@media (max-width: 585px) {
  /* 3.1 general resize by changing the font size in html*/
  html {
    font-size: 50%;
  }

  /* 3.2 Default box resizing  */
  .default-box {
    width: 95vw;
    height: 95vh;
  }

  /* 3.3. game area */
  /* 3.3.1 game area layout */

  .game-box-grid {
    grid-template-rows: 12rem 3fr 1fr;
  }

  /*message text */
  .text-box {
    margin: 0rem 2rem;
  }

  /* 3.3.1 memory blocks styling */
  /* 3.3.1.1 memory blocks styling width*/
  .memory-block-and-start .margin--30 {
    margin: 0rem 10%;
  }

  .memory-block-and-start .margin--25 {
    margin: 0rem 7.5%;
  }

  .memory-block-and-start .margin--20 {
    margin: 0rem 7.5%;
  }

  .memory-block-and-start .margin--15 {
    margin: 0rem 7.5%;
  }

  .memory-block-and-start .margin--10 {
    margin: 0rem 5%;
  }

  .memory-block-area {
    padding: 0rem 0rem 3rem 0rem;
  }

  /* 3.3.1.2 memory blocks styling height*/

  .memory-block-height--10 {
    height: 5rem;
  }

  .memory-block-height--5 {
    height: 3.5rem;
  }

  .memory-block-height--3 {
    height: 2.5rem;
  }

  /* 3.3.2 bottom area */

  /* center elements */
  .bottom-area {
    flex-wrap: wrap;
    justify-content: center;
  }

  .support-quit-button {
    flex-basis: 100%;
    justify-content: center;
  }

  /* switch position scorebox and quit button */
  .support-quit-button {
    order: 2;
  }

  .btn-quit {
    margin: 0rem 5rem 2rem 5rem;
  }

  .scores-box {
    margin: 0rem 5rem 2rem 5rem;
    padding: 1.5rem 2.5rem;
  }

  .high-score-text {
    top: -3rem;
  }

  /* 3.4 highscore modal */

  .high-score-modal-box {
    height: 60%;
    width: 90%;
  }

  /* 3.5 Difficulty select menu */
  .difficulty-buttons {
    flex-basis: 50%;
    gap: 6rem;
  }

  /* 3.5 Custom difficulty select menu */

  .btn-previous {
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0rem;
  }
}
